import Vue from 'vue'
import './plugins/axios'
import App from './App.vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)
const Home = { template: '<div>home</div>' }

const router = new VueRouter({
  mode: 'history',
  base: __dirname,
  routes: [
    { path: '/', component: App },
    { path: '/post/:id', component: Home }
  ]
})


Vue.config.productionTip = false



new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
